.create-portfolio-container {
  h2 {
    color: var(--text-black-900);
    margin-bottom: 20px;
    opacity: 0;
    animation: fadeInUp 1s 1s;
    animation-fill-mode: forwards;
  }

  .inp-portfolio {
    input {
      width: 100%;
      background-color: var(--bg-black-100);
      margin: 5px 0;
      opacity: 0;
      animation: fadeInUp 1s 1s;
      animation-fill-mode: forwards;
    }

    .file-inp {
      display: flex;
      align-items: center;
      gap: 18px;

      input {
        width: 87%;
        padding: 10px;
        box-shadow: var(--box-shadow-2);
      }
      .img-btn {
        background-color: var(--skin-color);
        height: 2.5rem;
        color: var(--bg-black-100);
        box-shadow: var(--box-shadow-2);
        opacity: 0;
        animation: fadeInUp 1s 1s;
        animation-fill-mode: forwards;
      }
    }

    .half-inp {
      display: flex;
      align-items: center;
      gap: 2%;

      textarea {
        width: 49%;
        resize: none;
        margin: 5px 0;
        opacity: 0;
        animation: fadeInUp 1s 1s;
        animation-fill-mode: forwards;
      }
    }

    .submit-btn {
      background-color: var(--skin-color);
      color: var(--bg-black-100);
    }
  }

  .text-editor {
    width: 100%;
    border: 1px solid var(--bg-black-50);
    margin: 5px 0;
    opacity: 0;
    animation: fadeInUp 1s 1s;
    animation-fill-mode: forwards;

    .text-aria {
      height: 300px;
      width: 100%;
      background-color: var(--bg-black-100);
      box-shadow: var(--box-shadow-2);
      font-family: "Poppins", sans-serif;
    }
  }

  #toolbar {
    background-color: var(--bg-black-100);
  }
}
