.dashboard-container {
  display: flex;
  gap: 20px;
  opacity: 0;
  animation: fadeInUp 1s 1s;
  animation-fill-mode: forwards;

  .post,
  .portfolio {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: var(--bg-black-100);
    padding: 10px 20px;
    border-radius: 5px;
    min-width: 200px;
    font-size: 20px;
    font-weight: 600;
    color: var(--text-black-900);
    box-shadow: var(--box-shadow-1);

    .icon {
      margin-right: 5px;
    }
  }
}
