@import "../../../components/Util/mixin";

.port-side-bar {
  width: 260px;
  height: 95vh;
  padding: 30px 10px;
  background: var(--bg-black-100);
  position: absolute;
  left: 10px;
  top: 15px;
  border-radius: 40px 10px;
  border-right: 1px solid var(--bg-black-50);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  @include shadow-dark;
  animation: fadeIn 0.5s 0.5s;
  opacity: 0;
  animation-fill-mode: forwards;

  .aside__logo {
    .logo__img {
      width: 100px;
      height: 100px;
      display: block;
      margin: 0 auto;
      img {
        width: 100%;
        border-radius: 50%;
        border: 3px solid var(--skin-color);
        transition: all 0.3s ease;
      }
    }
    .logo__text {
      max-width: 100%;
      h2 {
        color: var(--text-black-900);
        font-weight: 700;
        letter-spacing: 3px;
        text-align: center;

        span {
          font-family: "Clicker Script", cursive;
          color: var(--skin-color);
        }
      }
    }
  }

  .aside__main {
    max-width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    margin-top: 3rem;

    .nav-link {
      margin-bottom: 20px;
      padding: 0 20px;
      display: flex;
      align-items: center;
      font-size: 16px;
      color: var(--text-black-700);
      border-bottom: 1px solid var(--bg-black-50);
      transition: 0.3s ease-in-out all;
      opacity: 0;
      animation: fadeInUp 1s 1s;
      animation-fill-mode: forwards;

      :first-child {
        font-size: 14px;
        margin-right: 7px;
      }
      &:hover {
        transform: translateX(5px);
        color: var(--text-black-900);
      }
    }
  }

  .active {
    color: var(--skin-color) !important;
  }
}
