.login-container {
  display: flex;
  justify-content: center;
  align-items: center;
  vertical-align: middle;
  width: 100vw;
  height: 100vh;

  .form-containt {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 20px;
    width: 400px;
    min-height: 400px;
    background-color: var(--bg-black-900);
    box-shadow: var(--box-shadow-3);
    border-radius: 10px;

    h2 {
      color: var(--text-black-900);
      margin-bottom: 5px;
    }

    .login-inp {
      margin: 8px 0;
    }

    .login-btn {
      padding: 5px 15px;
      border: 1px solid var(--skin-color);
      margin-top: 15px;

      &:hover {
        background-color: var(--skin-color);
        color: white;
      }
    }
  }
}
