.blog-container {
  width: 100%;
  animation: fadeIn 0.5s 0.5s;
  opacity: 0;
  animation-fill-mode: forwards;

  .blog-header {
    h1 {
      border-bottom: 1px solid var(--skin-color);
      padding-bottom: 5px;
    }
    .sub-text {
      text-align: right;
      font-size: 12px;
      line-height: 16px;
      color: var(--text-black-700);
    }
  }

  .blogs {
    width: 100%;
    display: grid;
    grid-template-columns: auto auto;
    justify-content: center;
    align-items: center;

    article {
      background-color: var(--bg-black-100);
      width: 400px;
      height: 400px;
      margin: 2rem;
      padding: 10px;
      border-radius: 10px;
      box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
      opacity: 0;
      animation: backInRight 1s 1s;
      animation-fill-mode: forwards;

      img {
        width: 100%;
        height: 55%;
        object-fit: cover;
      }

      .blog-title {
        color: var(--text-black-900);
        transition: 0.3s all ease;
        font-size: 18px;

        &:hover {
          color: var(--text-black-700);
        }
      }

      .blog-info {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 10px;

        .views {
          display: flex;
          justify-content: center;
          align-items: center;
        }
      }
    }
  }
}

.single-blog-conainer {
  width: 100%;
  background-color: var(--bg-black-100);

  img {
    width: 100%;
  }

  h1 {
    color: var(--text-black-900);
    font-size: 1.8rem;
    margin: 10px 0;
    padding-bottom: 5px;
    border-bottom: 4px solid var(--bg-black-50);
  }

  .blog-body {
    color: var(--text-black-700);
  }
}
