@import "../../Util/mixin";

.home__page {
  display: flex;
  justify-content: center;

  .content {
    display: flex;
    flex-wrap: wrap;
  }

  .text__zone {
    @include padd__15;
    margin-left: 2rem;
    margin-top: 4rem;

    h1 {
      color: var(--text-black-900);
      font-size: 4.5rem;
      margin: 0;
      font-family: "Coolvetica";
      font-weight: 400;
      letter-spacing: 3px;

      &::before {
        content: "<h1>";
        font-family: "La Belle Aurore";
        color: var(--skin-color);
        font-size: 18px;
        position: absolute;
        margin-top: -1%;
        left: 40px;
        opacity: 0.6;
        animation: fadeIn 1s 1.7s backwards;
        -webkit-animation: fadeIn 1s 1.7s backwards;
      }
      &::after {
        content: "<h1>";
        font-family: "La Belle Aurore";
        color: var(--skin-color);
        font-size: 18px;
        position: absolute;
        margin-top: 65px;
        margin-left: 2px;
        opacity: 0.6;
        animation: fadeIn 1s 1.7s backwards;
        -webkit-animation: fadeIn 1s 1.7s backwards;
      }
    }

    h2 {
      color: var(--text-black-900);
      font-weight: 400;
      font-family: sans-serif;
      margin-top: 20px;
      letter-spacing: 3px;
      font-size: 12px;
      animation: fadeIn 1s 1.8s backwards;
      -webkit-animation: fadeIn 1s 1.8s backwards;
    }
  }

  .home__contact__btn {
    animation: fadeIn 2s 1.8s backwards;

    .contact-btn {
      border: 1.5px solid var(--skin-color);
      padding: 15px 20;
      margin-top: 20px;
      color: var(--skin-color);
      font-weight: 500;

      &:hover {
        color: white;
        background-color: var(--skin-color);
      }
    }
  }

  .tech-icon {
    height: 2.8rem;
    pointer-events: none;
  }

  .ferris {
    right: -1.5%;
    top: 10%;
    height: 80%;
    position: fixed;
    overflow: hidden;
  }

  .img-parent {
    padding: 50% 0;
  }
}
