@import "../../Util/mixin";

.section {
  overflow: auto;
}
.port-container {
  max-width: 100% !important;
  animation: fadeIn 0.5s 0.5s;
  opacity: 0;
  animation-fill-mode: forwards;

  .port-hrader {
    h1 {
      width: 100% !important;
      color: var(--text-black-900);
      font-size: 53px;
      margin: 0;
      font-family: "Coolvetica";
      font-weight: 400;
      border-bottom: 1px solid var(--skin-color);
    }
    p {
      text-align: right;
      font-size: 12px;
      line-height: 16px;
      color: var(--text-black-700);
    }
  }
  .port-main {
    .port-cards {
      display: grid;
      grid-template-columns: auto auto;
      justify-content: center;
      align-items: center;

      .port-card {
        background-color: var(--bg-black-100);
        width: 400px;
        height: 400px;
        margin: 2rem;
        padding: 10px;
        border-radius: 10px;
        box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
        opacity: 0;
        animation: backInRight 1s 1s;
        animation-fill-mode: forwards;

        img {
          width: 100%;
          height: 180px;
          object-fit: cover;
          border-radius: 10px;
        }

        .text {
          font-size: 14px;
          font-weight: 500;
          color: var(--text-black-700);

          p {
            margin: 10px 0;

            span {
              margin-right: 5px;
              font-size: 16px;
              font-weight: bold;
              color: var(--text-black-900);
            }
          }
          .title {
            font-size: 15px;
          }
        }

        .port-btn {
          border: 1px solid var(--skin-color);
          border-radius: 3px;
          padding: 7px 10px;
          color: var(--text-black-900);
          margin-top: 7px;
          font-weight: bold;

          &:hover {
            background-color: var(--skin-color);
            color: #fff;
          }
        }
      }
    }
  }
}
