.all-port-container {
  opacity: 0;
  animation: fadeInUp 1s 1s;
  animation-fill-mode: forwards;

  table {
    border-collapse: collapse;
    width: 100%;
    background-color: var(--bg-black-100);
    text-align: left;

    th,
    td {
      border: 1px solid var(--bg-black-50);
      padding: 0.5rem;
      color: var(--text-black-700);
    }

    tbody {
      font-size: 14px;
      img {
        width: 50px;
        height: 30px;
        object-fit: cover;
      }

      .port-action {
        background-color: var(--skin-color);
        color: white;
      }
      .delete-btn {
        background-color: rgb(206, 4, 4);
        margin-left: 10px;
      }
    }
  }
}
