.skill__page {
  animation: fadeIn 0s 1s;
  opacity: 0;
  animation-fill-mode: forwards;

  .text__zone {
    position: absolute;
    left: 10%;
    top: 50%;
    transform: translateY(-50%);
    width: 35%;
    vertical-align: middle;
    display: table-cell;
    max-height: 90%;
  }

  h1 {
    font-size: 53px;
    font-family: "Coolvetica";
    color: var(--text-black-900);
    font-weight: 400;
    margin-top: 0;
    margin-bottom: 40px;
    position: relative;
    left: 10px;
    letter-spacing: 3px;

    &::before {
      content: "<h1>";
      font-family: "La Belle Aurore";
      font-size: 18px;
      color: var(--skin-color);
      position: absolute;
      margin-top: -10px;
      left: -10px;
      opacity: 0.6;
      line-height: 18px;
    }
    &::after {
      content: "</h1>";
      font-family: "La Belle Aurore";
      font-size: 18px;
      color: var(--skin-color);
      position: absolute;
      opacity: 0.6;
      line-height: 18px;
      left: -30px;
      bottom: -20px;
      margin-left: 20px;
    }
  }

  p {
    font-size: 13px;
    color: var(--text-black-700);
    font-family: sans-serif;
    font-weight: 300;
    min-width: fit-content;
    animation: pulse 1s;
    margin: 7px 0;

    &:nth-of-type(1) {
      animation-delay: 1.1s;
    }
    &:nth-of-type(2) {
      animation-delay: 1.2s;
    }
    &:nth-of-type(3) {
      animation-delay: 1.3s;
    }
  }
}
.text-animate-hover {
  &:hover {
    color: var(--skin-color);
  }
}

.skill__charts {
  position: absolute;
  right: 0%;
  top: 10%;
  flex-direction: column;
  width: 50%;

  a {
    font-family: "Coolvetica" !important;
    font-weight: bold !important;
  }
}
