.search-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
  padding-bottom: 1rem;
  border-bottom: 1px solid var(--bg-black-50);
  opacity: 0;
  animation: fadeInUp 1s 1s;
  animation-fill-mode: forwards;

  .search {
    .search-inp {
      width: 350px;
      padding: 10px;
      border-radius: 5px;
      outline: none;
      border: none;
      box-shadow: var(--box-shadow-2);
      font-family: "Poppins", sans-serif;
      font-size: 14px;

      &::placeholder {
        font-size: 14px;
        font-family: "Poppins", sans-serif;
      }
    }
    .search-btn {
      padding: 7px;
      background-color: var(--skin-color);
      color: white;
      box-shadow: var(--box-shadow-2);
      margin-left: 15px;

      &:hover {
        background-color: #3d73ca;
      }
    }
  }

  .profile {
    .profile-btn {
      position: relative;
      width: 40px;
      height: 40px;
      border-radius: 50%;
      box-shadow: var(--box-shadow-3);
      border: 2px solid var(--skin-color);
      cursor: pointer;
    }

    .sub-menu-wrap {
      position: absolute;
      top: 60%;
      right: 0%;
      width: 250px;
      max-height: 0px;
      overflow: hidden;
      transition: max-height 0.3s;
      z-index: 1;

      .sub-menu {
        padding: 20px;
        margin: 10px;
        background-color: var(--bg-black-100);

        .user-info {
          display: flex;
          align-items: center;

          h3 {
            font-weight: 500;
          }
          img {
            width: 60px;
            border-radius: 70px;
            margin-right: 15px;
          }
        }
        hr {
          border: 0;
          height: 1px;
          width: 100%;
          background-color: var(--bg-black-50);
          margin: 15px 0 10px;
        }

        a {
          display: flex;
          align-items: center;
          color: var(--text-black-700);
          margin-bottom: 10px;
          font-size: 17px;

          span {
            margin-left: 10px;
          }
        }
      }
    }
    .sub-menu-wrap.open-menu {
      max-height: 400px;
    }
  }
}
