.contact-container {
  animation: fadeIn 0.5s 0.5s;
  opacity: 0;
  animation-fill-mode: forwards;

  .contact-header {
    h1 {
      border-bottom: 1px solid var(--skin-color);
    }
    .sub-text {
      text-align: right;
      font-size: 12px;
      line-height: 16px;
      color: var(--text-black-700);
    }
  }

  .contact-main {
    display: flex;
    margin-top: 10px;

    .contact-form {
      flex: 1;
      padding: 15px;

      .text-zone {
        color: var(--text-black-700);
        font-size: 14px;
        margin-bottom: 20px;
        opacity: 0;
        animation: fadeInUp 1s 1s;
        animation-fill-mode: forwards;
      }

      form {
        .half {
          display: flex;
          justify-content: center;
          align-items: center;
          margin-bottom: 15px;

          :nth-child(1) {
            margin-right: 10px;
          }

          .half-inp {
            width: 50%;
            opacity: 0;
            animation: fadeInUp 1s 1s;
            animation-fill-mode: forwards;
          }
        }

        .full {
          display: flex;
          justify-content: center;
          align-items: center;
          flex-direction: column;

          .full-inp {
            width: 100%;
            margin-bottom: 15px;
            opacity: 0;
            animation: fadeInUp 1s 1s;
            animation-fill-mode: forwards;
          }

          .full-textaria {
            width: 100%;
            opacity: 0;
            animation: fadeInUp 1s 1s;
            animation-fill-mode: forwards;
          }
        }

        .send-btn {
          border: 1.5px solid var(--skin-color);
          padding: 5px 30px;
          margin-top: 20px;
          color: var(--skin-color);
          font-weight: 500;
          opacity: 0;
          animation: fadeInUp 1s 1s;
          animation-fill-mode: forwards;

          &:hover {
            color: white;
            background-color: var(--skin-color);
          }
        }
      }
    }

    .contact-map {
      flex: 1;
      padding: 15px;
      position: relative;
      width: 100%;
      opacity: 0;
      animation: backInRight 1s 1s;
      animation-fill-mode: forwards;

      .info-map {
        position: absolute;
        background-color: var(--bg-black-50);
        backdrop-filter: blur(3px);
        color: var(--text-black-700);
        border-radius: 5px;
        padding: 10px;
        font-size: 16px;

        hr {
          color: var(--text-black-700);
          margin: 5px 0;
        }
      }
      .google-map {
        iframe {
          width: 100%;
          min-height: 440px;
          border: none;
          border-radius: 5px;
        }
      }
    }
  }
}
