.single-prot {
  width: 100%;
  animation: fadeIn 0.5s 0.5s;
  opacity: 0;
  animation-fill-mode: forwards;

  img {
    width: 100%;
  }

  .port-text {
    margin-top: 10px;

    .name {
      font-size: 1.6rem;
      color: var(--text-black-900);
    }
    .live-btn {
      background-color: transparent;
      border: 1px solid var(--skin-color);
      color: var(--text-black-700);
      margin: 10px 0;
      font-weight: 600;

      &:hover {
        background-color: var(--skin-color);
        color: #fff;
      }
    }
    .title {
      margin: 5px 0;
      color: var(--text-black-900);
      font-size: 1.3rem;
    }

    .desc {
      margin-bottom: 5px;
      color: var(--text-black-700);
      font-size: 15px;
    }
    .all-pages {
      margin: 20px 0;

      h3 {
        color: var(--text-black-900);
        font-size: 1.1rem;
        margin-bottom: 5px;
      }
      ul {
        margin-left: 20px;
        list-style: inside;
        color: var(--text-black-700);
        font-weight: 500;
      }
    }

    .package {
      background-color: var(--bg-black-100);
      padding: 10px;
      border-radius: 10px;

      p {
        font-weight: 500;
        color: var(--text-black-900);

        span {
          font-weight: bold;
          font-size: 20px;
        }
      }
      .pkg {
        color: var(--text-black-700);
      }
    }
    .pkg-client {
      margin-bottom: 20px;
    }
  }
}
