.user-container {
  opacity: 0;
  animation: fadeInUp 1s 1s;
  animation-fill-mode: forwards;

  table {
    border-collapse: collapse;
    border: 1px solid var(--bg-black-50);
    background-color: var(--bg-black-100);
    box-shadow: var(--box-shadow-3);
    padding: 30px;
    width: 100%;

    tr,
    td {
      padding: 10px 30px;
      text-align: center;
    }

    tbody {
      img {
        width: 50px;
        border-radius: 50%;
      }

      .action-btn {
        background-color: red;
        color: white;
        padding: 7px 10px;
      }
    }
  }
}
