@import url("https://fonts.googleapis.com/css2?family=Clicker+Script&family=Dancing+Script:wght@400;700&family=Poppins:wght@200;300;400;500;600&display=swap");

/* font-family: 'Clicker Script', cursive;
font-family: 'Dancing Script', cursive;
font-family: 'Poppins', sans-serif; */

@import "./components/Util/mixin";
@import "animate.css";
@import "~loaders.css/src/animations/pacman.scss";

@font-face {
  font-family: "Helvetica Neue";
  src: url("./assets/fonts/helvetica-neu.ttf") format("ttf");
}

@font-face {
  font-family: "La Belle Aurore";
  src: url("./assets/fonts/LaBelleAurore.woff2") format("woff2");
}

@font-face {
  font-family: "Coolvetica";
  src: url("./assets/fonts/CoolveticaRg-Regular.woff2") format("woff2");
}

:root {
  --bg-black-900: #f2f2fc;
  --bg-black-100: #fdf9ff;
  --bg-black-50: #e8dfec;
  --text-black-900: #302e4d;
  --text-black-700: #504e70;
  --skin-color: #5e8bd4;
  --box-shadow-1: rgba(0, 0, 0, 0.16) 0px 1px 4px;
  --box-shadow-2: rgba(0, 0, 0, 0.1) 0px 10px 15px -3px,
    rgba(0, 0, 0, 0.05) 0px 4px 6px -2px;
  --box-shadow-3: rgba(0, 0, 0, 0.1) 0px 10px 15px -3px,
    rgba(0, 0, 0, 0.05) 0px 4px 6px -2px;
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  max-width: 1200px;
  height: 100vh;
  font-family: "Poppins", sans-serif;
}

a {
  text-decoration: none;
}

button {
  border: none;
}
input,
textarea {
  font-family: "Helvetica Neue";
}

.section {
  background: var(--bg-black-900);
  max-height: 95vh;
  display: block;
  padding: 0 30px;
  opacity: 1;
  position: fixed;
  left: 295px;
  right: 10px;
  top: 15px;
  bottom: 0;
  border-radius: 10px 40px;
  transition: all 0.3s ease;
  @include shadow-dark;
  animation: fadeIn 0.6s 0.6s;
  opacity: 0;
  animation-fill-mode: forwards;
}

.container {
  max-width: 1100px;
  width: 100%;
  margin: auto;
  padding-top: 20px;
  padding-bottom: 20px;
}
.header-h1 {
  color: var(--text-black-900);
  font-size: 53px;
  margin: 0;
  font-family: "Coolvetica";
  font-weight: 400;
}
.common-input {
  padding: 10px;
  border-radius: 5px;
  outline: none;
  border: none;
  box-shadow: var(--box-shadow-2);
  font-family: "Poppins", sans-serif;
  font-size: 14px;

  &::placeholder {
    font-size: 14px;
    font-family: "Poppins", sans-serif;
  }
}
.common-btn {
  padding: 8px;
  background-color: var(--skin-color);
  color: white;
  box-shadow: var(--box-shadow-2);

  &:hover {
    background-color: #3d73ca;
  }
}
